@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300;400;500;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
button {
  font-family: "Darker Grotesque";
  color: #474d46;
  letter-spacing: 2px;
}
h1 {
  font-size: 6rem;
  font-weight: 500;
}
h2 {
  font-size: 4rem;
}
h5 {
  font-size: 1.5rem;
}
p {
  color: #474d46;
  font-family: "Darker Grotesque";
  font-size: 1.3rem;
}
a {
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 3px;
  text-transform: uppercase;
}
img {
  width: 100%;
}
input {
  padding: 18px;
  background-color: #f2f5f1;
  border: 2px white solid;
  width: 400px;
  font-family: "Darker Grotesque";
  color: #474d46;
  font-size: 1.2rem;
}
.section-space {
  padding: 100px 0px;
}
hr.divider {
  width: 80px;
  border-top: 4px solid #81988e;
  border-radius: 30px;
}
.btn-outline-primary {
  color: #474d46;
  border-color: #474d46;
  font-size: 1rem;
  border-radius: 0px;
  font-weight: 600;
  border-width: 2px;
  letter-spacing: 4px;
  padding: 10px 14px;
}
.btn-outline-primary:hover {
  color: #fff;
  background-color: #474d46;
  border-color: #474d46;
}
.full-container {
  background-color: #f2f5f1;
}
.full-container .row {
  height: 100vh;
}
.background-gray {
  background-color: #f2f5f1;
}
.background-dark-gray {
  background-color: #dfe4de;
}

@media (max-width: 990px) {
  input {
    width: 300px;
  }
}

.navbar {
  position: absolute;
  width: 100%;
}
.nav-link {
  margin-right: 40px;
}
.nav-login {
  margin-left: 15px;
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
  color: rgba(0, 0, 0, 0.5);
}
.nav-login:hover {
  color: rgba(0, 0, 0, 0.7);
}
nav .navbar-collapse {
  -webkit-justify-content: flex-end !important;
          justify-content: flex-end !important;
  transition: opacity 1s ease-out;
  opacity: 1;
  z-index: 2;
}
.logo {
  font-size: 2rem;
  font-weight: 700;
}
@media (max-width: 990px) {
  .collapse.navbar-collapse {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    position: absolute;
    margin-top: 60px;
    width: 90%;
    left: 5%;
  }
}

.nav-footer {
  text-align: right;
}
.nav-footer a {
  margin: 20px;
}
.social-icons .icon-circle {
  display: inline-block;
  margin: 0px 8px;
  background-color: #484d47;
  padding: 5px;
  width: 35px;
  height: 35px;
  text-align: center;
  color: #fff;
  border-radius: 50px;
}
.social-icons .icon-circle:hover {
  background-color: #646963;
  color: #fff;
}
.icons-container {
  float: left;
}
.nav-container {
  float: right;
}
@media (max-width: 767px) {
  footer .social-icons,
  footer .nav-footer {
    text-align: center;
  }
  footer .nav-footer a {
    display: block;
  }
  footer .row .col-sm:first-child {
    float: right;
  }
  footer .nav-container {
    float: left;
    width: 100%;
    display: block;
    margin-bottom: 80px;
  }
  footer .icons-container {
    float: right;
    width: 100%;
    display: block;
  }
}

#banner-home h2 {
  font-size: 6rem;
  font-weight: 500;
}
#banner-home {
  background-image: url(/landing-exercise/static/media/banner.6f79cdc1.jpg);
  padding: 15rem 0px;
  background-size: 100%;
  background-position: top;
  background-repeat: no-repeat;
}
.title {
  margin: 10px auto;
}
@media (max-width: 990px) {
  #banner-home {
    background-size: auto 100%;
  }
}
@media (max-width: 767px) {
  #banner-home {
    padding: 10rem 0px;
  }
}

.card-container {
  margin: 20px;
  padding: 0px;
}
.card-container .card {
  border: none;
}

#about-section p {
  max-width: 500px;
  margin: 0 auto;
  line-height: 30px;
  color: #8a8a8a;
}
.unfilled {
  padding: 0px;
  margin: 0px;
}

.profile-card {
  margin-top: 8rem !important;
}

